import Vue from 'vue';
import { Axios } from '../server';  // Vue-Axios ajax
import SHA256 from 'sha256';      // SHA256
import ls from '../utils/ls';    // localStorage
import utils from '../utils/utils';     // 

// 
let currentDate = utils.formatDate(new Date(), '-').timerStr;

const baseUrl = '/apis/';   // dev
// const baseUrl = ''; 

const config = {
  tokenRequest: `${baseUrl}token?`,   // tokenhttp
  apiRequest: `${baseUrl}api?`,       // apihttp
  validMethod: 'token_acquire',       //  AAT | UAT | URT, 
  unvalidMethod: 'token_nullify',     //  AAT | UAT | URT, 
  refreshMethod: 'token_refresh',     //  AAT | UAT | URT, 
  appId: 'shlib.web.h5.1',              // app id => 
  appsecret: 'FE588C8B064805CF3AF5FB722EAF4419D7B67574', // app secret => , 
  // deviceId: 'hqxu20171028',             // id
  deviceId: utils.guid(),             // id
  aat: ls.getItem('AAT'),   //  AAT 
  uat: ls.getItem('UAT'),   //  UAT 
  urt: ls.getItem('URT'),   //  URT 
  uatexpiretime: 'uat_1'    // uat1/71
};

/**
 * [tokenHandle ]
 * @param getAAT => AAT
 * @param getUATURT =>  UAT | URT
 * @param refreshUATURT =>  UAT | URT
 * @param nullifyAATUAT => AAT | UAT
 * @param getUATURTThirdPart =>  UAT | URT
 * @param validcheckToken => token
 * @param lsURTUAT => URT | UAT
 */
export const tokenHandle = {
  getAAT () {
    let self = this;
    let date = utils.formatDate().timerStr;
    let secret = SHA256(config.appId + date + config.appsecret);
    return Axios({
      url: config.tokenRequest,
      params: {
        method: config.validMethod,
        type: 'aat',
        appid: config.appId,
        //secret,
        deviceid: config.deviceId
      }
    })
    .then(res => {
      const AAT = res.aat;
      config.aat = AAT;
      self.validcheckToken('aat')
      .then(inner => {
        config.aat = AAT;
        ls.setKeysList({
          AAT: AAT,
          AAT_exptime: Date.parse(res.expiretime_aat)
        });
        //ls.setItem('AAT', AAT); // AAT
        //ls.setItem('AAT_exptime', Date.parse(res.expiretime_aat)); // AAT
      });
    });
  },
  getUATURT ({username ='', password = '', tokentype = ''}) {
    let self = this;
    return Axios({
      url: config.tokenRequest,
      params: {
        method: config.validMethod,
        type: 'uat',
        username,
        password,
        tokentype,
        aat: config.aat
      }
    }).then(res => {
      config.uat = res.uat;
      config.urt = res.urt;
      // UAT | URT
      ls.setKeysList({
        UAT: res.uat,
        URT: res.urt,
        UAT_exptime: Date.parse(res.expiretime_uat),
        URT_exptime: Date.parse(res.expiretime_urt),
        shlibBorrower: res.us.shlibBorrower, //  ''
        loginUserInfo: {}
      });
    })
  },
  refreshUATURT () {
    let self = this;
    return Axios({
      url: config.tokenRequest,
      params: {
        method: config.refreshMethod,
        aat: config.aat,
        urt: config.urt
      }
    }).then(res => {
      config.urt = res.urt;
      config.uat = res.uat;
      self.lsURTUAT(res);
    })
  },
  lsURTUAT (res) {
    // UAT | URT
    ls.setKeysList({
      UAT: res.uat,
      URT: res.urt,
      UAT_exptime: Date.parse(res.expiretime_uat),
      URT_exptime: Date.parse(res.expiretime_urt),
    });
  },
  nullifyAATUAT (tokentype) {
    let self = this;
    // AAT
    let params = {
      method: config.unvalidMethod,
      type: tokentype,
      deviceid: config.deviceId,
      appid: config.appId
    };

    // UAT
    if (tokentype === 'uat') {
      params = Object.assign({}, params, {
        uat: config.uat
      });
    }
    return Axios({
      url: config.tokenRequest,
      params
    }).then(() => {
      if (tokentype === 'uat') {
        config.uat = null;
        ls.removeList(['UAT', 'URT', 'loginUserInfo', 'UAT_exptime', 'URT_exptime']);
      } else {
        config.aat = null;
        ls.removeList(['AAT', 'AAT_exptime']);
      }
    });
  },
  getUATURTThirdPart (code) {
    let self = this;
    return Axios({
      url: config.tokenRequest,
      params: {
        method: config.validMethod,
        type: 'uat_oauth',
        aat: config.aat,
        code: code,
        tokentype: 'uat_7'
      }
    }).then(res => {
      self.lsURTUAT(res);
    })
  },
  validcheckToken (tokentype) {
    /**
     * AAT | UAT | URT
     * @param {String} tokentype: 'aat','uat','urt'
     */
    let tokenvalue = '';
    switch (tokentype) {
      case 'aat':
        tokenvalue = config.aat;
        break;
      case 'uat':
        tokenvalue = config.uat;
        break;
      case 'urt':
        tokenvalue = config.urt;
        break;
      default: break;
    }

    return Axios({
      url: config.tokenRequest,
      params: {
        method: 'token_validcheck',
        tokentype,
        tokenvalue
      }
    })
    .catch(err => {
      console.error('token !');
    });
  }
}

function checkToken () {
  if (!config.aat) {
    tokenHandle.getAAT();
  }
}

checkToken();
