import Vue from 'vue';
import Vuex from 'vuex';
import Router from 'vue-router';
// pages
import index from '@/page/index'; // 
import homePage from '@/page/homePage'; // 
import myLibrary from '@/page/myLibrary'; // 
// import myLibraryDefault from '@/page/myLibraryDefault'; // 
// import recommendedBook from '@/page/recommendedBook'; // 
// import museumNavigation from '@/page/museumNavigation'; // 
import fullMenu from '@/page/fullMenu'; // 
// import fullMenuDefault from '@/page/fullMenuDefault'; // 
// import currentLending from '@/page/currentLending'; // 
// import libraryList from '@/page/libraryList'; // 
import newsBulletin from '@/page/newsBulletin'; // 
// import newsBulletinDefault from '@/page/newsBulletinDefault'; // 
// import newsDetails from '@/page/newsDetails'; // 
import serviceGuide from '@/page/serviceGuide'; // 
// import serviceGuideDefault from '@/page/serviceGuideDefault'; // 
// import serviceIssueList from '@/page/serviceIssueList'; // 
// import serviceIssueDetails from '@/page/serviceIssueDetails'; // 
// import serviceIssueSearch from '@/page/serviceIssueSearch'; // 
// import pBookDetails from '@/page/pBookDetails'; // 
// import pBookSearch from '@/page/pBookSearch'; // 
// import activityCenter from '@/page/activityCenter'; // 
// import eventDetails from '@/page/eventDetails'; // 
// import commonPage from '@/page/commonPage'; // 
import eBook from '@/page/eBook'; // 
import eBookDefault from '@/page/eBookDefault'; // 
import bookClassification from '@/page/bookClassification'; // 
// import bookDefault from '@/page/bookDefault'; // 
import bookClassificationList from '@/page/bookClassificationList'; // 
// import myBookshelf from '@/page/myBookshelf'; // 
// import eBookSearch from '@/page/eBookSearch'; // 
// import recommended from '@/page/recommended'; // 
// import recommendedPastDefault from '@/page/recommendedPastDefault'; // 
import recommendedPast from '@/page/recommendedPast'; // 
// import eBookDetails from '@/page/eBookDetails'; // 
// import weiWeek from '@/page/weiWeek'; // 
// import digitalReadingList from '@/page/digitalReadingList'; // 
// import eBookCommonPage from '@/page/eBookCommonPage'; // 

Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [{
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      // name: 'index',
      meta: {
        requireAuth: false,      // 
        breadcrumbName: ''   // 
      },
      component: index,
      children: [{
        path: '',
        name: 'homePage',
        component: homePage
      }, {
        path: 'myLibrary',
        // name: 'myLibrary',
        meta: {
          requireAuth: true,
          breadcrumbName: ''
        },
        component: myLibrary,
        children: [{
          path: '',
          name: 'myLibraryDefault',
          component: resolve => require(['@/page/myLibraryDefault'], resolve)
        }, {
          path: 'recommendedBook',
          name: 'recommendedBook',
          meta: {
            requireAuth: true,
            breadcrumbName: ''
          },
          component: resolve => require(['@/page/recommendedBook'], resolve)
        }]
      }, {
        path: 'museumNavigation',
        name: 'museumNavigation',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: resolve => require(['@/page/museumNavigation'], resolve)
      }, {
        path: 'fullMenu',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: fullMenu,
        children: [{
          path: '',
          name: 'fullMenuDefault',
          component: resolve => require(['@/page/fullMenuDefault'], resolve)
        },{
          path: 'currentLending',
          name: 'currentLending',
          meta: {
            requireAuth: true,
            breadcrumbName: ''
          },
          component: resolve => require(['@/page/currentLending'], resolve)
        }]
      }, {
        path: 'libraryList',
        name: 'libraryList',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: resolve => require(['@/page/libraryList'], resolve)
      }, {
        path: 'newsBulletin',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: newsBulletin,
        children: [{
          path: '',
          name: 'newsBulletinDefault',
          component: resolve => require(['@/page/newsBulletinDefault'], resolve)
        }, {
          path: 'newsDetails',
          name: 'newsDetails',
          meta: {
            requireAuth: false,
            breadcrumbName: ''
          },
          component: resolve => require(['@/page/newsDetails'], resolve)
        }]
      }, {
        path: 'serviceGuide',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: serviceGuide,
        children: [{
          path: '',
          name: 'serviceGuideDefault',
          component: resolve => require(['@/page/serviceGuideDefault'], resolve)
        }, {
          path: 'serviceIssueList',
          name: 'serviceIssueList',
          meta: {
            requireAuth: false,
            breadcrumbName: ''
          },
          component: resolve => require(['@/page/serviceIssueList'], resolve)
        }, {
          path: 'serviceIssueDetails',
          name: 'serviceIssueDetails',
          meta: {
            requireAuth: false,
            breadcrumbName: ''
          },
          component: resolve => require(['@/page/serviceIssueDetails'], resolve)
        }, {
          path: 'serviceIssueSearch',
          name: 'serviceIssueSearch',
          meta: {
            requireAuth: false,
            breadcrumbName: ''
          },
          component: resolve => require(['@/page/serviceIssueSearch'], resolve)
        }]
      }, {
        path: 'pBookDetails',
        name: 'pBookDetails',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: resolve => require(['@/page/pBookDetails'], resolve)
      }, {
        path: 'pBookSearch',
        name: 'pBookSearch',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: resolve => require(['@/page/pBookSearch'], resolve)
      }, {
        path: 'activityCenter',
        name: 'activityCenter',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: resolve => require(['@/page/activityCenter'], resolve)
      }, {
        path: 'eventDetails',
        name: 'eventDetails',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: resolve => require(['@/page/eventDetails'], resolve)
      }, {
        path: 'commonPage',
        name: 'commonPage',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: resolve => require(['@/page/commonPage'], resolve)
      }]
    }, {
        path: '/eBook',
        name: '',
        meta: {
          requireAuth: false,
          breadcrumbName: ''
        },
        component: eBook,
        children: [{
          path: '',
          name: 'eBookDefault',
          component: eBookDefault
        }, {
            path: 'bookClassification',
            meta: {
              requireAuth: false,
              breadcrumbName: ''
            },
            component: bookClassification,
            children: [{
              path: '',
              name: 'bookDefault',
              component: resolve => require(['@/page/bookDefault'], resolve)
            }, {
              path: 'bookClassificationList',
              name: 'bookClassificationList',
              meta: {
                requireAuth: false,
                breadcrumbName: ''
              },
              component: resolve => require(['@/page/bookClassificationList'], resolve)
            }]
          },{
            path: 'myBookshelf',
            name: 'myBookshelf',
            meta: {
              requireAuth: true,
              breadcrumbName: ''
            },
            component: resolve => require(['@/page/myBookshelf'], resolve)
          }, {
            path: 'eBookSearch',
            name: 'eBookSearch',
            meta: {
              requireAuth: false,
              breadcrumbName: ''
            },
            component: resolve => require(['@/page/eBookSearch'], resolve)
          }, {
            path: 'recommendedPast',
            meta: {
              requireAuth: false,
              breadcrumbName: ''
            },
            component: recommendedPast,

            children: [{
              path: '',
              name: 'recommendedPastDefault',
              component: resolve => require(['@/page/recommendedPastDefault'], resolve),
            },{
              path: 'recommended',
              name: 'recommended',
              meta: {
                requireAuth: false,
                breadcrumbName: ''
              },
              component: resolve => require(['@/page/recommended'], resolve),
              }]
          }, {
            path: 'eBookDetails',
            name: 'eBookDetails',
            meta: {
              requireAuth: false,
              breadcrumbName: ''
            },
            component: resolve => require(['@/page/eBookDetails'], resolve),
          }, {
            path: 'weiWeek',
            name: 'weiWeek',
            meta: {
              requireAuth: false,
              breadcrumbName: ''
            },
            component: resolve => require(['@/page/weiWeek'], resolve),
          }, {
            path: 'digitalReadingList',
            name: 'digitalReadingList',
            meta: {
              requireAuth: false,
              breadcrumbName: ''
            },
            component: resolve => require(['@/page/digitalReadingList'], resolve)
          }, {
            path: 'eBookCommonPage',
            name: 'eBookCommonPage',
            meta: {
              requireAuth: false,
              breadcrumbName: ''
            },
            component: resolve => require(['@/page/eBookCommonPage'], resolve)
          }
        ]
    }
  ]
});

var _hmt = _hmt || [];
var _czc = _czc || [];
router.beforeEach((to, from, next) => {
  if (_hmt&&to.path) {  
    _hmt.push(['_trackPageview', to.fullPath]);  
    if(console)
      console.log('' + to.fullPath);
  }  
  if (_czc) {       
     _czc.push(['_trackPageview', to.fullPath]);
     if(console)
      console.log('' + to.fullPath);
  }
  if (to.matched.some(r => r.meta.requireAuth)) {
      if (Vue.prototype.ls.getItem('loginUserInfo')) {
          next();
      }
      else {
        // console.log(to);
        // console.log(from);
        // console.log(next);
        // login
        if (to.matched[0]) {
          next({
            path: to.matched[0].path,
            query: {
              login: Vue.prototype.utils.guid()
            }
          });
        } else {
          next({
            path: 'index',
            query: {
              login: Vue.prototype.utils.guid()
            }
          });
        }
      }
  }
  else {
      next();
  }
});

export default router;


